<template>
  <div id="app">
    <transition name="fadeIn">
      <keep-alive>
        <!--需要缓存的-->
        <router-view v-if="$route.meta.keepAlive"></router-view>
      </keep-alive>
    </transition>
    <transition name="fadeIn">
      <!--不需要缓存的，正常访问的走下面-->
      <router-view v-if="!$route.meta.keepAlive"></router-view>
    </transition>



    <Tab></Tab>
  </div>
</template>

<script>
  import Tab from './base/Tab'
export default {
  name: 'App',
  components:{
    Tab
  }
}
</script>

<style>
  /*公共样式*/
*{
  margin: 0;
  padding: 0;
}
ul,li{
  list-style: none;
}
  a{
    text-decoration: none;
  }
  input,button{
    -webkit-appearance: none;
  }
  .content{
    position: fixed;
    width: 100%;
    top: 40px;
    bottom: 50px;
    overflow: auto;
  }

  /*路由动画*/
  .fadeIn-enter{
    opacity: 0;
  }
  .fadeIn-enter-active{
    transition: all 0.3s linear;
  }
  .fadeIn-leave-active{
    transition: all 0.3s linear;
    opacity: 0;
  }
</style>



